        p.mb-0 Esta es quizs la mejor contribucin que se ha hecho para la mejora de los procesos logsticos dentro de las organizaciones, pues permite tener el control de los diferentes bienes y servicios, establecer procesos de negociacin, tener manejo frente al almacenamiento de materias primas e insumos de productos acabados y piezas semielaboradas, soportados por informacin tcnica documentada, el uso de canales de distribucin adecuados, etc. La implementacin de  dichas cadenas permite que se entregue al cliente un producto o se le ofrezca un servicio con las mejores condiciones y que este satisfaga sus expectativas.
    .row.align-items-end
