            h3.txt-c1.mb-0 #[b Qu es una cadena de suministros o abastecimiento?]
        p La definimos como la filosofa conceptual que realiza integracin, dotacin y comercializacin de todos los componentes tecnolgicos a centros de almacenamiento, distribuidores y detallistas, con el objeto de que los bienes sean manufacturados y mercadeados en las cantidades adecuadas, en los sitios y en los momentos correctos, con renta para todas las entidades involucradas y con prcticas amigables en los niveles de servicio requeridos para satisfacer las condiciones del cliente final.
        .cajon.color-primario.p-4
          p.mb-0 Toda empresa est inmersa, de alguna forma, en alguna cadena de suministro, ya sea como proveedor, como cliente, pues abriga las necesidades de los canales de distribucin de determinado producto (envo, carga, operatividad, entre otros factores).
