        AcordionA(tipo="b" clase-tarjeta="tarjeta tarjeta--azul")
          .row.align-items-center(titulo="Hazlo por ti mismo (<i>Ford</i>)")
            .col.mb-4.mb-lg-0
              p.mb-0 Manejo total de la cadena. La empresa integra el suministro de materiales e insumos, inclusive la construccin de infraestructura tecnolgica y mecnica para su poca.
            .col-auto
