          .row.align-items-center(titulo="Estrategia de integracin vertical")
            .col.mb-4.mb-lg-0
              p.mb-0 Constituye una filosofa que incorpora diferentes reas para garantizar el suministro de materias primas e insumos; con dicha incorporacin, se realiza un favorecimiento para solucionar necesidades sentidas, afianzar la competitividad de bienes y servicios, elevar la calidad y aumentar competitividad.
            .col-auto
              img(src='@/assets/curso/tema1/img14.svg', style="width: 110px" ,alt='')
          .row.align-items-center(titulo="Descentralizacin")
