    .titulo-segundo.color-acento-contenido
      h2 1.2 Tcnicas de aplicacin de la cadena de suministros
    .row.align-items-center.mb-4
      .col-lg-8.mb-4.mb-lg-0
        p Las entidades y corporaciones de amplio recorrido soportan las estructuras jerrquicas con base en departamentos que separan sus funciones para mayor productividad, reas de sistema de gestin de la calidad, ingeniera, economa, finanzas, gerencia, entre otras, las cuales implementan sistemas estratgicos de accin autnoma, sistemas de incentivos, filosofas colaborativas de integracin con proveedores, acciones, que de alguna manera, generan medidas de choque integrales para obtener ventajas competitivas.
        p La aplicacin administrativa de la cadena de suministro radica en la planificacin, la organizacin y la vigilancia eficaz y productiva de los flujos en y entre los mecanismos de la cadena, es una malla compleja y dinmica de subestructuras, corporaciones y personas con objetivos diferentes pero alineados con la planeacin estratgica.
        .cajon.color-primario.p-4
          p.mb-0 En la aplicacin tcnica de una cadena de suministros, uno de los puntos de mayor beneficio para los sectores productivos se identifica, entre otras cosas, con la difcil tarea de predecir la demanda para la compra acertada de materiales e insumos, pues las condiciones de comportamiento de los mercados globales y regionales es incierta precisamente por la gran variedad de productos y sus mnimos ciclos de vida.
