      .col-10.col-md-8.col-lg.bg-c5.m-2
        .p-4
          figure.mb-4
            img(src='@/assets/curso/tema1/img11.svg', style="width: auto" ,alt='')
          p #[b Pronsticos]
          p.mb-0 Construir con precisin pronsticos del estado real de la infraestructura para establecer relacin de compromisos anticipados con los clientes.
      .col-10.col-md-8.col-lg.bg-c5.m-2
        .p-4
          figure.mb-4
            img(src='@/assets/curso/tema1/img12.svg', style="width: auto" ,alt='')
          p #[b Soporte tcnico]
          p.mb-0 Estructurar soporte tcnico para la creacin de reas del servicio post venta y en atencin a requerimientos del cliente.
    .row.justify-content-center.mb-5
      .col-10.col-md-8.col-lg.bg-c5.m-2
        .p-4
          figure.mb-4
            img(src='@/assets/curso/tema1/img13.svg', style="width: auto" ,alt='')
          p #[b Planes de accin]
          p.mb-0 Establecer con anticipacin planes de accin para atender altas demandas; entrega satisfactoria de los pedidos, en relacin a las necesidades de flujo de manufactura de bienes y servicios, en atencin al aprovisionamiento de materias primas e insumos.
      .col-10.col-md-8.col-lg.bg-c5.m-2
        .p-4
          figure.mb-4
            img(src='@/assets/curso/tema1/img14.svg', style="width: auto" ,alt='')
          p #[b Control]
          p.mb-0 Mantener el control del proceso con respecto al perfeccionamiento de nuevos productos y su venta.
      .col-10.col-md-8.col-lg.bg-c5.m-2
        .p-4
          figure.mb-4
            img(src='@/assets/curso/tema1/img15.svg', style="width: auto" ,alt='')
          p #[b Estrategias sostenibles]
          p.mb-0 Someter a reestructuracin los procesos logsticos existentes para implementar estrategias sostenibles y amigables en miras al cuidado del medio ambiente.
    p El montaje de sistemas de gestin de calidad del producto es hoy una obligacin, la competitividad exige que los productos y servicios estn en el tiempo requerido, al costo negociado y en el lugar donde se solicit.
    .row.bg-c6.mb-5
      .col-12
        .px-5.py-4.py-lg-0
          .row.align-items-center
            .col-lg-8.mb-4.mb-lg-0
