            img(src='@/assets/curso/tema1/img17.svg', style="width: auto" ,alt='').m-auto
        .col-lg-8
          .bloque-texto-a__texto.p-4
            p Cuando se encadenan las diferentes reas logsticas y locativas con el uso indiscriminado de infraestructuras digitales y de procedimiento, se asume la creacin e integracin organizada de una red logstica en la que circula informacin documentada y digital con objetivos claros frente al aumento extremo de la productividad.
              br
              br
              | El prototipo, montaje y diseo de la red logstica, se caracteriza por mostrarse de acuerdo con un argumento valioso: contar con la infraestructura logstica adecuada para cubrir las demandas del mercado de una manera rpida y beneficiosa.
    .row.align-items-center.mb-4
      .col-lg-4.mb-4.mb-lg-0
        figure
          img(src='@/assets/curso/tema1/img8.png', style="width: auto" ,alt='').m-auto
      .col-lg-8
        p La creacin y disposicin para los departamentos internos de una estructurada de red logstica, repercute en la configuracin para implementar flexibilidad y capacidad de acogerse a principios de uso de energas renovables y de cadenas de suministro sostenibles y empoderadas con las decisiones de mantenerse al tanto de surgimiento de tecnologas emergentes; an ms con el diseo de esta clase de redes logsticas, las decisiones de oficio sern de alta trascendencia y, con ellas, las organizaciones permanecern mucho ms largo tiempo.
        p.mb-0 La ubicacin locativa de las reas, los principios elementales de acopio y las polticas institucionales de envo de bienes manufacturados, son componentes concluyentes en el desarrollo de la cadena de suministro. Las cifras indiscutibles en la decisin del total de las locaciones, las reas de distribucin, posicin, forma y tamao son los componentes claves del diseo de la red logstica de cualquier emprendimiento. Implica  entender, la operatividad, facilidad de transporte y flexibilidad competitiva del proceso.
    p.mb-5 Para garantizar el ptimo funcionamiento de la red logstica, la organizacin debe contar con el nmero pertinente de colaboradores atendindola y las condiciones ptimas de distribucin y operatividad dentro de la empresa, por ende, las entidades que hacen construccin de una red logstica de suministro deben tener en cuenta una serie de criterios, tales como los que se exponen en el siguiente recurso:
    SlyderF(columnas="col-md-6 col-xl-4")
      .tarjeta.color-acento-contenido.p-4
        .row.justify-content-center.mb-3
          .col-6
            img(src='@/assets/curso/tema1/img18.svg' alt='AvatarTop')
        p.text-center Se debe establecer por dnde comenzar la implementacin de la red logstica.
      .tarjeta.color-acento-contenido.p-4
        .row.justify-content-center.mb-3
          .col-6
            img(src='@/assets/curso/tema1/img19.svg' alt='AvatarTop')
        p.text-center Se debe diagnosticar si por costos se pueden unificar varios departamentos en una sola red.
      .tarjeta.color-acento-contenido.p-4
        .row.justify-content-center.mb-3
          .col-6
            img(src='@/assets/curso/tema1/img20.svg' alt='AvatarTop')
        p.text-center Se debe identificar la importancia de la aplicacin de las estrategias de la red, el rol que desempea cada uno.
      .tarjeta.color-acento-contenido.p-4
        .row.justify-content-center.mb-3
          .col-6
            img(src='@/assets/curso/tema1/img21.svg' alt='AvatarTop')
        p.text-center Se debe garantizar que las redes no queden sobrecargadas estructuralmente, es decir deben quedar bien dimensionadas.
      .tarjeta.color-acento-contenido.p-4
        .row.justify-content-center.mb-3
          .col-6
            img(src='@/assets/curso/tema1/img22.svg' alt='AvatarTop')
        p.text-center Se debe determinar el nivel de inventario a sostener.
      .tarjeta.color-acento-contenido.p-4
        .row.justify-content-center.mb-3
          .col-6
            img(src='@/assets/curso/tema1/img23.svg' alt='AvatarTop')
        p.text-center Se debe determinar los conglomerados de clientes, colaboradores, consumidores, segmentos, que se pueden atender.
      .tarjeta.color-acento-contenido.p-4
        .row.justify-content-center.mb-3
          .col-6
            img(src='@/assets/curso/tema1/img24.svg' alt='AvatarTop')
        p.text-center Se debe garantizar que la estructura de la red logstica sea lo suficientemente slida y con buen grado de flexibilidad para acondicionarse a las necesidades y cambios sostenibles de los consumidores y compradores.
      .tarjeta.color-acento-contenido.p-4
        .row.justify-content-center.mb-3
          .col-6
            img(src='@/assets/curso/tema1/img25.svg' alt='AvatarTop')
        p.text-center Se debe pronosticar la distancia y el tiempo de planificacin.
      .tarjeta.color-acento-contenido.p-4
        .row.justify-content-center.mb-3
          .col-6
            img(src='@/assets/curso/tema1/img26.svg' alt='AvatarTop')
        p.text-center Se debe garantizar el acondicionamiento de los procesos sostenibles y amigables, energas renovables y tecnologas emergentes.
      .tarjeta.color-acento-contenido.p-4
        .row.justify-content-center.mb-3
          .col-6
            img(src='@/assets/curso/tema1/img27.svg' alt='AvatarTop')
        p.text-center El nuevo sistema plantea catalogar las mnimas posibilidades de realizar malas inversiones, y de elevadas variaciones en los costos.
