    p Puntualmente, se establecen parmetros estandarizados que determinan las unidades o el total a adquirir de materiales e insumos, segn sus caractersticas y especificidades en fichas tcnicas. Son delimitados por el consumo, las medidas de negociacin y fechas de entrega y el ciclo de abastecimiento, lo que facilita la seleccin y evaluacin de proveedores, algunos parmetros pertinentes para contactarlos son:
