              p.mb-0 Cuando las entidades corporativas dan comienzo a nuevos emprendimientos y proyectos empresariales, ponerlos en funcionamiento debe desplegar una completa cantidad de estrategias para suplir elementos que no pongan en aprietos su funcionamiento. Se deben tener en cuenta diseos, colores y estilos, inicios de temporada, nuevas tendencias, entre otros; estos elementos condicionan la seleccin precisa de insumos y materias primas.
    p.mb-5 Al analizar de manera detenida el procedimiento de seleccin y compra de insumos, los departamentos de compras tienen presente otros condicionamientos:
    .row.bg-c3.mb-5
