    p.mb-5 Las plantas fabriles y de servicio realizan esquemas de informacin que se validan a travs de un sistema digital de registro y de estructuracin y que permiten la clasificacin de inventarios de los materiales segn la normatividad establecida; estos esquemas facilitan:
    .row.bg-c4.mb-5
      .col-12
        .p-5
          .row.mb-3
            .col-auto
              h3.txt-c1.mb-0 #[b Ventajas de los esquemas de informacin]
          .row.align-items-center
            .col-lg-4.mb-4.mb-lg-0
              figure
                img(src='@/assets/curso/tema1/img45.svg', style="width: auto" ,alt='').m-auto
            .col-lg-8
              .tarjeta.tarjeta--azul
                .p-4
                  SlyderA
                    .row.justify-content-center
                      .col-10
                        p.mb-0 #[b 1. Equilibrio]
                        p Facilita la disposicin de inventarios disminuyendo la cantidad de bienes y su diversidad a nmeros equilibrados.
                        p.mb-0 #[b 2. Trazabilidad]
                        p Elimina las imperfecciones del sistema al volverse digital y no manual; en tiempo real establece trazabilidad porcentual a los puntos de equilibrio requeridos para el proceso y las improvisaciones son cosas del pasado por la tipificacin, exacta y transparente de los datos de los materiales.
                        p.mb-0 #[b 3. Orden]
                        p Maximiza el uso del espacio fsico al tener que almacenar solo lo necesario, registra digitalmente las reas requeridas y cae en una ordenacin eficiente de los inventarios, facilitando la labor cuando se requiere asignarlos.
                        p.mb-0 #[b 4. Exactitud]
                        p Sin lugar a dudas el clculo con exactitud de las necesidades de inventario mejora ostensiblemente.
                    .row.justify-content-center
                      .col-10
                        p.mb-0 #[b 5. Facilidad]
                        p Crea la poltica instruccional de concentrarse en menor inventario de artculos y se lucra el grupo de colaboradores en administrar ms fcilmente el proceso.
                        p.mb-0 #[b 6. Eficiencia]
                        p Disminuye la elaboracin de inventarios de manera manual.
                        p.mb-0 #[b 7. Control]
                        p Se tiene un control ms preciso respecto a la rotacin de productos, identificando el de mayor acogida por el cliente o el que necesita ser transformado.
                        p.mb-0 #[b 8. Efectividad]
                        p Contribuye en disminucin de errores de despacho de insumos y materiales.
                        p.mb-0 #[b 9. Rapidez]
                        p Facilita el desarrollo de tareas y los procesos administrativos, la informacin se encuentra consignada en un solo lugar y, por ende, se adquiere con mayor rapidez y facilidad.
    p.mb-5 El diseo de fichas tcnicas para la identificacin de cada material existente permite la conformacin de una base de datos que facilita su clasificacin y ordenamiento. La reparticin de los materiales, en conformidad con la planificacin de procesos logsticos, justifican la utilizacin de un esquema de smbolos, cdigos, colores, imgenes, patrones y elaboracin de matrices de tolerancia y equivalencia que facilitan acciones de identificacin, clasificacin y ordenamiento de forma precisa gil y fcil.
    .row.mb-4
      .col-auto
        h3.txt-c1.mb-0 #[b Gestin de compras sostenibles]
    .row.bg-c2.align-items-center.mb-5.p-4.p-lg-0
      .col-md-auto
        figure
          img(src='@/assets/curso/tema1/img46.svg', style="width: auto" ,alt='').m-auto
      .col
        p.mb-0 La misin que implica la normatividad de compra sostenible, como estrategia de desarrollo sustentable amigable de buenas prcticas de compra, es un reto para todas las empresas, pues esta adems de disminuir costos y el uso excesivo de materiales e insumos, disminuye los impactos ambientales que pueden afectar las generaciones futuras; adems, es un compromiso con la sociedad y el medio ambiente. A continuacin, la definicin ms precisa de compra sostenible.
    .row.align-items-center.mb-5
      .col-lg-8.mb-4.mb-lg-0
        .row
          figure.fig-p1
            img(src='@/assets/curso/tema1/img32.svg', style="width: auto" ,alt='').m-auto
          .col-12.bg-c7
            .p-4
              p.text-white #[b Compra sostenible]
              p.text-white Hace referencia a la integracin de parmetros ticos, y de transparencia en la adquisicin de bienes y servicios que puedan emplearse en un proceso logstico y que no generen daos significativos al medio ambiente.
          figure.fig-p2
            img(src='@/assets/curso/tema1/img33.svg', style="width: auto" ,alt='').m-auto
        .cajon.color-acento-botones.p-4
          p.mb-0 Recordar que: cuando se encuadra un marco de referencia de compra sostenible, se diferencia del consumo sostenible, porque es el resultado final del primero, la responsabilidad es del que produce y en menor parte activa del que consume.
      .col-lg-4
        figure
          img(src='@/assets/curso/tema1/img47.svg', style="width: auto" ,alt='').m-auto
    .row.mb-4
      .col-auto
        h3.txt-c1.mb-0 #[b Polticas para proveedores sostenibles]
    p.mb-5 La legislacin referente a la compra sostenible es casi nula en entornos nacionales, est primordialmente enlazada a las adquisiciones del sector pblico. Honestamente, en relacin al carcter netamente voluntario de gran parte de las iniciativas coligadas a la compra sostenible, las administraciones no han desarrollado normativas o leyes que exijan a las empresas a promover prcticas sostenibles en su gestin de compras, ms all del acatamiento de la Ley. Se presentan algunas consideraciones que se han dado al respecto:
    .tarjeta.tarjeta--azul.mb-5
      .p-4.p-md-5
        SlyderA(tipo="b")
          .row.m-1.align-items-center
            .col-lg-4.mb-4.mb-lg-0
              p #[b Polticas: proveedores sostenibles]
              p A continuacin, se presentan algunas consideraciones sobre la poltica europea que se han dado en materia de sostenibilidad empresarial. 
              p En el continente europeo, las incipientes decisiones que hacen referencia a la contratacin responsable o sostenible desde el sector pblico, se encumbran a los aos 80, aunque hasta los aos 90 no se pone visible la jerarqua y el potencial de la compra pblica con criterios ambientales.
            .col-lg-8
              figure
                img(src='@/assets/curso/tema1/img10.png', alt='')
          .row.m-1.align-items-center
            .col-lg-4.mb-4.mb-lg-0
              p #[b Polticas: proveedores sostenibles]
              p En el Convenio de msterdam del ao 1997, se registra que la integracin del factor medioambiental en todas las polticas corporativas es condicin relevante para lograr un desarrollo sustentable y sostenible. A partir de esa dcada surgen las primeras subestructuras de compra sustentable y limpia, y se echan a rodar proyectos de convenio pblico verde a nivel regional, nacional e internacional.
            .col-lg-8
              figure
                img(src='@/assets/curso/tema1/img11.png', alt='')
          .row.m-1.align-items-center
            .col-lg-4.mb-4.mb-lg-0
              p #[b Polticas: proveedores sostenibles]
              p En el perodo 2003, la Comisin Europea, en su comunicacin sobre Poltica Integrada de Productos, exhibe una tctica para reforzar y reorientar la poltica medioambiental relativa a los productos, con esencia de promover el perfeccionamiento de un mercado de productos ms ecolgicos y crear los escenarios generales para que los productos sean ms considerados con el medio a lo largo de todo su ciclo de vida.
            .col-lg-8
              figure
                img(src='@/assets/curso/tema1/img12.png', alt='')
          .row.m-1.align-items-center
            .col-lg-4.mb-4.mb-lg-0
              p #[b Polticas: proveedores sostenibles]
              p Ante el limbo legal existente sobre la posibilidad e importancia de reunir criterios ambientales y sociales en los contratos pblicos de forma correcta, se desarrollaron y aprobaron dos Directivas, que en la actualidad regulan el marco jurdico de la contratacin pblica a nivel de la Unin Europea.
              p Directiva 2004/17/EC: 31 de marzo de 2004, coordina los procedimientos de contratacin de entidades que operan en el sector del agua, la energa, el transporte y los servicios postales.
              p Directiva 2004/18/EC: 31 de marzo de 2004, coordina los procedimientos de adjudicacin de contratos pblicos de obra, suministro y servicio.
            .col-lg-8
              figure
                img(src='@/assets/curso/tema1/img13.png', alt='')
    .row.mb-4
      .col-auto
        h3.txt-c1.mb-0 #[b Registro y evaluacin y reevaluacin de proveedores]
    .row.bg-c9.align-items-center.mb-5
      .col-lg-auto.pt-5.pt-lg-0
        figure
          img(src='@/assets/curso/tema1/img14.png', style="width: auto" ,alt='').m-auto
      .col
        .p-4
          p Actualmente, un sinnmero de   empresas, en su mayora multinacionales, han introducido polticas de compromiso social sostenible encaminadas a sus proveedores, y en las que se les comunica a stos de los principios y valores ticos tomados por la empresa, as como de la responsabilidad que se espera del proveedor en este sentido, tanto directamente como en su propia cadena logstica de suministro, sub-proveedores, productores, entre otros, especialmente llamativa. 
          p En este sentido, como ejemplo, est la poltica adoptada por el gigante sueco del equipamiento, IKEA, por el rumbo de colaboracin para con los proveedores. Sin embargo, en sus inicios, la multinacional fue fuertemente criticada por haber impuesto inesperadamente a sus proveedores unas condiciones muy estrictas para las que no estaban preparados, generando as cierto grado de catstrofe industrial, en muchos de los casos, en naciones frgiles.
          p.mb-0 Como contestacin a tales crticas, la multinacional ajust sus polticas y estndares, dando pie a la asistencia, la trazabilidad y el margen de tiempo para acoplarse.
    p.mb-5 La descomposicin en valores corporativos y transparencia es otra de las preocupaciones, por lo que cada proveedor debe comprometerse a firmar un pacto o declaracin en contra de corrupcin, junto con un manual que integre polticas de transparencia en la firma de contratos. Adems de una poltica anticorrupcin, se instan tambin al acatamiento de unas reglas para la prevencin de sta, as como una poltica de pesquisa que describe cmo los colaboradores y proveedores deben proceder ante dudas de estafas, corrupcin, fraude u otras experiencias ilcitas.
    .row.mb-4
      .col-auto
        h3.txt-c1.mb-0 #[b Documentacin, ciclo de vida del bien o servicio]
    .row.bg-c10.align-items-center.mb-5
      .col-lg-8
        .p-4
          p Cuando se nombran las diferentes etapas por la que pasa un producto a partir de su creacin y con destino a un punto de venta, se puede pronosticar la propensin en el avance de las ventas de un determinado bien, pero se debe tener presente factores definidos, porque no todos los productos son similares. Ms an, se deben tener en consideracin las particularidades del mercado en el que se distribuye y de la entidad que lo procesa. 
          p.mb-0 A continuacin se exponen las fases del ciclo de vida del producto listo para su comercializacin:
      .col-lg-4.mb-4.mb-lg-0
        figure
          img(src='@/assets/curso/tema1/img15.png', style="width: auto" ,alt='').m-auto
    TabsA.color-acento-contenido.mb-5
      .tarjeta.color-acento-contenido--borde.p-4(titulo="Fase nmero uno")
        .row.align-items-center
          .col-lg-8.mb-4.mb-lg-0
            p #[b Introduccin]
            p.mb-0 Es el instante en el que se crea el bien y se pone en marcha en espacios comerciales. En esta fase, los clientes o compradores potenciales desconocen el bien, incluso puede que no coexista una necesidad determinada ni una demanda que justifique su aparicin. En derivacin de lo anterior, la demanda es mnima. Para fomentar la inclusin del producto al mercado es indispensable promocionar con campaas publicitarias.
          .col-lg-4
            figure
              img(src='@/assets/curso/tema1/img50.svg', style="width: auto" ,alt='').m-auto
      .tarjeta.color-acento-contenido--borde.p-4(titulo="Fase nmero dos")
        .row.align-items-center
          .col-lg-8.mb-4.mb-lg-0
            p #[b Crecimiento]
            p.mb-0 La particularidad transcendental de esta etapa es el incremento de las demandas. A medida que el bien se va dando a conocer, acrecienta el inters de los clientes y, a la vez, los beneficios. Para fortalecer este incremento, la empresa debe acrecentar la logstica para satisfacer la peticin del mercado. La competencia puede aportar productos al mercado que generan competitividad.
          .col-lg-4
            figure
              img(src='@/assets/curso/tema1/img51.svg', style="width: auto" ,alt='').m-auto
      .tarjeta.color-acento-contenido--borde.p-4(titulo="Fase nmero tres")
        .row.align-items-center
          .col-lg-8.mb-4.mb-lg-0
            p #[b Madurez]
            p.mb-0 Es la fase de mximos histricos, o de entierro del producto, en el que se logra la trazabilidad en ventas del bien. El impulso debe concentrarse en mejorar la atencin de los clientes, con el propsito de cumplir con las expectativas previstas. Es la etapa en la que ms tcticas se colocan en camino para que el bien siga vigente en la mente del cliente.
          .col-lg-4
            figure
              img(src='@/assets/curso/tema1/img52.svg', style="width: auto" ,alt='').m-auto
      .tarjeta.color-acento-contenido--borde.p-4(titulo="Fase nmero cuatro")
        .row.align-items-center
          .col-lg-8.mb-4.mb-lg-0
            p #[b Declive]
            p.mb-0 Es la etapa final del ciclo de vida porque el producto malgasta su inters ante los clientes y los negocios caen en picada. Es el instante de las sper ofertas y rebajas. En este mismo camino, se debe situar la estrategia publicitaria a notificar esas oportunidades de lograr el producto a bajo costo.
          .col-lg-4
            figure
              img(src='@/assets/curso/tema1/img53.svg', style="width: auto" ,alt='').m-auto
    .row.mb-4
      .col-auto
        h3.txt-c1.mb-0 #[b Estrategias y oportunidades de mitigacin]
    p.mb-5 Una subestructura de mitigacin es un instrumento que reconoce y soluciona los acontecimientos que suelen ser peligrosos y que se prev que ocurrirn en determinado proyecto.  Se describen algunas estrategias para el manejo de diferentes riesgos dentro de las organizaciones.
    .row.align-items-center
      .col-lg-6.mb-4.mb-lg-0
        figure
          img(src='@/assets/curso/tema1/img54.svg', style="width: auto" ,alt='').m-auto
      .col-lg-6
        LineaTiempoD.color-primario
          .row(numero="1" titulo="Aceptacin del riesgo")
            .col-12
              p Se identifica y se reconoce el riesgo, pero, se determina que estos no afectan el desarrollo de objetivos empresariales y que pueden ser aceptados.
              p Es necesario aclarar que s es importante desarrollar estrategias para convertir dichos riesgos en procesos adaptables dentro de las tareas que se llevan dentro de la organizacin.
          .row(numero="2" titulo="Evitacin del riesgo")
            .col-12
              p La forma de evitar el riesgo es impidiendo que se lleve a cabo la actividad que lo genera, por ende, se debe buscar una estrategia o actividad que implique un riesgo menor.
          .row(numero="3" titulo="Mitigacin del riesgo")
